import React from 'react';
import './signup.css'
export class A extends React.Component{
    constructor(props){
        super(props);
        this.state={
            username:this.props.username,
            userphone:this.props.userphone,
            useremail:this.props.useremail,
            password:this.props.password,
            conf_password:this.props.password,
            error:'',
            otp:'',
            otpsend:false,
            mobileverified:false
        }
    }
    change = (e) =>{
        this.setState(
            {
                [e.target.name]:e.target.value,
                errorField:""
            },
            ()=>{
                if(e.target.value.length<5){
                    this.setState({
                        error:"Length should be greater than 5",
                        errorField:"username"
                    })
                }
                if(e.target.value.length<10){
                    this.setState({
                        error:"You need to enter valid phone number",
                        errorField:"userphone"
                    })
                }
                
            }
        )
    }
    submit = (e) =>{
        e.preventDefault()
        if(this.state.username && this.state.userphone && this.state.useremail && this.state.password && this.state.conf_password){
            if(this.state.password===this.state.conf_password){
                var headers = new Headers()
                headers.append("Cookie","sessionid=qbnklw4k1p8ke3ed1gd6z1a5m796iaj1; csrftoken=DgO5MwMCZ0QfveQ9nFnZLTe9hMTQxGYw1rW3dICnHKC2z7CRahDGB88M8sEG8bUE")

                var formdata = new FormData()

                formdata.append("user_name",this.state.username);
                formdata.append("user_phone",this.state.userphone);
                formdata.append("user_email",this.state.useremail);
                formdata.append("password",this.state.password);

                fetch(`http://localhost:8000/fin/register`,{
                    method:'POST',
                    headers:headers,
                    body:formdata,
                    redirect:'follow'
                }).then(response => response.json())
                .then(data => console.log(data.token));
            }
        }
    }
    sendotp = (e) =>{
        e.preventDefault();
        this.setState({otpsend:true})
        if(!this.state.otpsend){
            var headers = new Headers()
            headers.append("Cookie","sessionid=qbnklw4k1p8ke3ed1gd6z1a5m796iaj1; csrftoken=DgO5MwMCZ0QfveQ9nFnZLTe9hMTQxGYw1rW3dICnHKC2z7CRahDGB88M8sEG8bUE")
            var formdata = new FormData()
            formdata.append("user_phone",this.state.userphone);
            fetch(`http://localhost:8000/fin/otpsend`,{
                method:'POST',
                headers:headers,
                body:formdata,
                redirect:"follow"
            })
            .then(response => response.json())
            .then(data=>{
                if(data.detail==="phone no already exists"){
                    this.setState({
                        error:"phone no already exists",
                        errorField:"userphone"
                    })
                }
                else if(data.detail==="error occur in sending otp"){
                    this.setState({
                        errorField:"userphone",
                        error:"error occur in sending otp"
                    })
                }
                if(data.detail==="otp send successfully"){
                    this.setState({otpsend:true})
                }
            })
        }
    }
    verifyotp = (e) =>{
        e.preventDefault();
        var headers = new Headers()
        headers.append("Cookie","sessionid=qbnklw4k1p8ke3ed1gd6z1a5m796iaj1; csrftoken=DgO5MwMCZ0QfveQ9nFnZLTe9hMTQxGYw1rW3dICnHKC2z7CRahDGB88M8sEG8bUE")
        var formdata = new FormData()
        formdata.append("user_phone",this.state.userphone);
        formdata.append("otp",this.state.otp);
        fetch(`http://localhost:8000/fin/otpsend`,{
            method:'POST',
            headers:headers,
            body:formdata,
            redirect:"follow"
        }).then(response => response.json())
        // .then(data => {
        //     if
        // })

    }
    render(){
        return(
            <div className="signup">
                <div className="left-side">
                    {/* <img src={side} alt="form"/> */}
                    <h3>Signup Page</h3>
                </div>
                <div className="form-main">
                <form onSubmit={this.submit} className="form-all">
                    <div className="form-in">
                        <label>Username</label>
                        <input onChange={this.change} type="text" name="username" value={this.state.username}></input>
                        { this.state.errorField ==="username"? this.state.error: null}
                    </div>
                    <div className="form-in">
                        <label>UserPhone</label>
                        <div className="form-phone">
                            <input onChange={this.change} type="text" name="userphone" value={this.state.userphone}></input>
                            <button onClick={this.sendotp} className="otp-btn">send otp</button>
                        </div>
                        {
                            this.state.otpsend ? 
                            <div className= " otp-verify"><label>Verify Otp</label>
                            <div className="form-phone">
                                <input onChange={this.change} type="text" name="otp" value={this.state.otp}></input>
                                <button onClick={this.verifyotp} className="otp-btn">Verify otp</button>
                            </div></div>: null
                        }
                        {this.state.errorField ==="userphone" ? this.state.error :null}
                    </div>
                    <div className="form-in">
                        <label>UserEmail</label>
                        <input onChange={this.change} type="email" name="useremail" value={this.state.useremail}></input>
                        {this.state.errorField==="useremail" ? this.state.error : null}
                    </div>
                    <div className="form-in">
                        <label>Password</label>
                        <input onChange={this.change} type="password" name="password" value={this.state.password}></input>
                        {this.state.errorField==="password" ? this.state.error : null}
                    </div>
                    <div className="form-in">
                        <label>Conform Password</label>
                        <input onChange={this.change} type="password" name="conf_password" value={this.state.conf_password}></input>
                        {this.state.errorField==="conf_password" ? this.state.error : null}
                    </div>
                    <div className="form-submit"><button className="otp-btn">Proceed</button></div>
                </form></div>
            </div>
        )
    }
}