import {Switch,Route} from 'react-router-dom';
import './index.css';
import { Dashboard } from './Dashboard/Dashboard';
import { Customers } from './Receipts/Customers';
import { Users } from './ManageUser/Users';
import { Bidding } from './Bidding_Settings/Bidding';
import { Account } from './Account_Settings/Account';
export const Index = () =>{
    return(
        <div className="main">
            <div className="navbar">
                <h1>MainView</h1>
            </div>
            {/* <Routes/> */}
            <Switch>
                <Route path="/home/dashboard" component={Dashboard}/>
                <Route path="/home/receipts" component={Customers}/>
                <Route path="/home/manageuser" component={Users}/>
                <Route path="/home/bidding" component={Bidding}/>
                <Route path="/home/account" component={Account}/>
                {/* <Route path="/register" component={A}/> */}
            </Switch>
        </div>
    )
}