import './Account.css'
import { Component } from 'react'
export class Account extends Component{
    constructor(props){
        super(props);
        this.state={
            mode :"User",
            otpsend:false,
            phoneotp:false
            // details:true,
            // password:false
        }
    }
    changetopass = e =>{
        this.setState({
            mode:"Password",
            email:false,
            phone:false
        })
    }
    changetouser =e =>{
        this.setState({
            mode:"User",
        })
    }
    changetopic=e=>{
        this.setState({
            mode:"Profile",
            email:false,
            phone:false
        })
    }
    sendotp =e=>{
        e.preventDefault();
        this.setState({email:true,phone:false})
        this.refs.phone.removeAttribute("disabled");
        this.refs.mail.setAttribute("disabled", "disabled");
    }
    phoneotp =e=>{
        e.preventDefault();
        this.setState({phone:true,email:false});
        this.refs.mail.removeAttribute("disabled");
        this.refs.phone.setAttribute("disabled","disabled");
    }
    render(){
        var main;
        if(this.state.mode==="User"){
            main=
            <div className="changes"> 
                <form>
                    <div className="form-r">
                        <label className="label">Username</label><br/>
                        <div className="input-data">
                            <input type="text" onChange={this.Change} id="username"></input> 
                            <button className="linebtn">Change</button><br/>
                        </div>
                    </div>
                    <div className="form-r">
                        <label className="label">Useremail</label><br/>
                        <div className="input-data">
                            <input type="email" onChange={this.Change} id="useremail"></input> 
                            <button ref="mail" className="linebtn" onClick={this.sendotp}>send otp</button><br/>
                        </div>
                    </div>
                    {this.state.email ?<div className="form-r">
                                    <label className="label">Verify Email</label><br/>
                                    <div className="input-data">
                                        <input type="text" onChange={this.Change} id="useremail"></input>
                                        <button className="linebtn">Verify</button>
                                    </div></div>
                                :null}
                    <div className="form-r">
                        <label className="label">Userphone</label><br/>
                        <div className="input-data">
                            <input type="tell" onChange={this.Change} id="userphone"></input> 
                            <button ref="phone" className="linebtn" onClick={this.phoneotp}>Get otp</button><br/>
                        </div>
                    </div>
                    {this.state.phone ?<div className="form-r">
                                    <label className="label">Verify Phone</label><br/>
                                    <div className="input-data">
                                        <input type="text" onChange={this.Change} id="useremail"></input>
                                        <button className="linebtn">Verify</button>
                                    </div></div>
                                :null}
                </form>
            </div>
        }
        else if(this.state.mode==="Password"){
            main=
            <div className="changes">
                <form>
                    <div className="form-r">
                        <label className="label">Old Password</label><br/>
                        <div className="input-data">
                            <input type="password" onChange={this.Change} id="old_pass"></input> 
                            <br/>
                        </div>
                    </div>
                    <div className="form-r">
                        <label className="label">New Password</label><br/>
                        <div className="input-data">
                            <input type="password" onChange={this.Change} id="new_pass"></input> 
                            <br/>
                        </div>
                    </div>
                    <div className="form-r">
                        <label className="label">Confirm Password</label><br/>
                        <div className="input-data">
                            <input type="password" onChange={this.Change} id="conf_pass"></input> 
                            <br/>
                        </div>
                    </div>
                    <div className="passbtn">
                        <button>Update</button>
                    </div>
                </form>
            </div>
        }
        else if(this.state.mode==="Profile"){
            main=
            <div className="profile">
                <form>
                    <div className="form-r">
                        {/* <img src=""/> */}
                        <div className="input-data">
                            <input type="file" ></input>
                        </div>
                    </div>
                </form>
            </div>
        }
        return(
            <div className="account">
                <div>
                    <div className="nav-links">
                        <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 0L0.59 1.41L5.17 6L0.59 10.59L2 12L8 6L2 0Z" fill="black"/>
                        </svg>
                        <p>Account Settings</p>
                        {/* {main} */}
                    </div>
                    <div className="update">
                        <ul className="list">
                            <li className="list-li" onClick={this.changetouser}><h5>Basic Details Update</h5>{this.state.mode==="User" ?<hr className="about-border"/>:null}</li>
                            <li className="list-li"   onClick={this.changetopass}><h5>Password update</h5>{this.state.mode==="Password" ?<hr className="about-border"/>:null}</li>
                            <li className="list-li"   onClick={this.changetopic}><h5>Dp update</h5>{this.state.mode==="Profile" ?<hr className="about-border"/>:null}</li>
                        </ul>
                        {main}
                    </div>
                </div>
            </div>
        )
    }
}